import React, { useState } from 'react';
import './filter.css';
import { BiChevronDown } from 'react-icons/bi';

import categories from '../../data/categories';

const Filter = (props) => {
    //console.log(categories);
    const categoriesElements = categories.map((el) => (
        <Category
            category={el}
            key={el.id}
            change={props.change}
            currentCat={props.currentCat}
        />
    ));
    return (
        <div className="filter">
            <h3 className="filter__title">Kategorie</h3>
            {categoriesElements}
        </div>
    );
};

export default Filter;

function Category(props) {
    const { displayName, subcategories } = props.category;
    const [expand, setExpand] = useState(false);

    const reveal = () => {
        setExpand((prev) => !prev);
    };

    function handleClick(e) {
        //console.log(e.target.value);

        //update subcategory in Offert.js
        props.change(e.target.value);
    }

    const subcatsElements = subcategories.map((el) => {
        const btnClass =
            props.currentCat === el.category
                ? 'category__btn category__btn--selected'
                : 'category__btn';
        return (
            <button
                className={btnClass}
                key={el.name}
                value={el.category}
                onClick={handleClick}>
                {el.name}
            </button>
        );
    });

    const categoryClass = expand ? 'category category--expanded' : 'category';

    return (
        <div className={categoryClass}>
            <div className="category__header" onClick={reveal}>
                <h4 className="category__title">{displayName}</h4>
                <BiChevronDown className="category__icon" />
            </div>
            <div className="category__subcategories">{subcatsElements}</div>
        </div>
    );
}
