import React from 'react';
import './bike-rental.css';

import bikesBg from '../../assets/images/bikes-bg.svg';

const BikeRental = () => {
    return (
        <div className="section rental" id="rental">
            <h1 className="section__title">Wypoycz rower</h1>
            <img src={bikesBg} alt="" className="rental__img" />
            <div className="section__container">
                <p className="rental__text">
                    Oferujemy Pastwu moliwo wypoyczenia rowerw
                    turystycznych dla dorosych i dzieci:
                </p>
                <div className="rental__types">
                    <p className="rental__type">MTB</p>
                    <p className="rental__type">miejskie</p>
                    <p className="rental__type">damskie</p>
                    <p className="rental__type">mskie</p>
                    <p className="rental__type">modzieowe</p>
                </div>
                <p className="rental__text">
                    W trosce o bezpieczestwo Pastwa i Waszych pociech w
                    wypoyczalni oferujemy kaski i foteliki dla dzieci
                </p>
                <h4 className="rental__title">Cennik wypoyczenia</h4>
                <div className="rental__prices">
                    <div className="rental__cost">
                        <p className="rental__time">1 godzina</p>
                        <h1 className="rental__rental__price">20 PLN</h1>
                    </div>
                    <div className="rental__cost">
                        <p className="rental__time">1 dzie</p>
                        <h1 className="rental__rental__price">70 PLN</h1>
                    </div>
                    <div className="rental__cost">
                        <p className="rental__time">1 tydzie</p>
                        <h1 className="rental__rental__price">250 PLN</h1>
                    </div>
                </div>
                <p className="rental__text">Kask + 15 PLN</p>
                <p className="rental__text">Fotelik dla dziecka + 30 PLN</p>
            </div>
        </div>
    );
};

export default BikeRental;
