import React, { useState } from 'react';
import './contact.css';

import { BiTrash, BiPhoneCall } from 'react-icons/bi';
import ContactSvg from '../../assets/images/man-talking-on-the-telephone.png';

const Contact = () => {
    const [input, setInput] = useState({
        firstName: '',
        lastName: '',
        email: '',
        message: '',
    });
    const [submitReady, setSubmitReady] = useState(false);

    const handleInput = (e) => {
        const { name, value } = e.target;
        setInput((prev) => {
            return {
                ...prev,
                [name]: value,
            };
        });

        setSubmitReady(validateInputs());
        console.log(submitReady);
    };

    const resetInput = () => {
        setInput((prev) => {
            let inputs = { ...prev };
            for (let key in inputs) {
                inputs[key] = '';
            }
            return inputs;
        });
    };

    function validateInputs() {
        for (let key in input) {
            if (key === 'email') {
                return validateEmail(input[key]);
            }
            if (input[key] == '') {
                return false;
            }
        }

        function validateEmail(email) {
            return String(email)
                .toLowerCase()
                .match(
                    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                );
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault();
    };

    return (
        <section className="section" id="contact">
            <h1 className="section__title">Skontaktuj si</h1>
            <div className="section__container contact__container">
                <form
                    action=""
                    onSubmit={handleSubmit}
                    className="contact__form">
                    <h3 className="contact__form-title">Napisz do nas</h3>
                    <div className="contact__name">
                        <div className="contact__group">
                            <input
                                type="text"
                                name="firstName"
                                id="firstname"
                                className="contact__input"
                                onChange={handleInput}
                                value={input.firstName}
                            />
                            <label
                                htmlFor="firstname"
                                className="contact__label">
                                Imi
                            </label>
                        </div>
                        <div className="contact__group">
                            <input
                                type="text"
                                name="lastName"
                                id="lastname"
                                className="contact__input"
                                onChange={handleInput}
                                value={input.lastName}
                            />
                            <label
                                htmlFor="lastname"
                                className="contact__label">
                                Nazwisko
                            </label>
                        </div>
                    </div>
                    <div className="contact__group">
                        <input
                            type="text"
                            name="email"
                            id="email"
                            className="contact__input"
                            onChange={handleInput}
                            value={input.email}
                        />
                        <label htmlFor="email" className="contact__label">
                            Adres Email
                        </label>
                    </div>
                    <div className="contact__group">
                        <textarea
                            name="message"
                            id="message"
                            className="contact__message"
                            onChange={handleInput}
                            value={input.message}></textarea>
                        <label htmlFor="message" className="contact__label">
                            Twoja Wiadomo
                        </label>
                    </div>
                    <div className="contact__buttons">
                        <button className="contact__btn" onClick={resetInput}>
                            <BiTrash />
                        </button>
                        <button
                            type="submit"
                            className="contact__btn"
                            id="contact__submit">
                            Wylij
                        </button>
                    </div>
                </form>
                <div className="contact__call">
                    <h3 className="contact__form-title">Zadzwo</h3>
                    <div className="contact__numbers">
                        <div className="contact__phone">
                            <a href="tel:876-433-945" className="contact__link">
                                <BiPhoneCall />
                            </a>
                            <p className="contact__number">+48 876 433 945</p>
                        </div>
                        <div className="contact__phone">
                            <a href="tel:578-918-015" className="contact__link">
                                <BiPhoneCall />
                            </a>
                            <p className="contact__number">+48 578 918 015</p>
                        </div>
                    </div>
                    <img src={ContactSvg} alt="" className="contact__image" />
                </div>
            </div>
        </section>
    );
};

export default Contact;
