import React from 'react';
import './product.css';

const Product = (props) => {
    const { name, subcategory, price, img } = props.card;

    return (
        <div className="product">
            <div className="product__container">
                <img src={img} alt="" className="product__image" />
            </div>
            <h4 className="product__name">{name}</h4>
            {/* <p className="product__category">{subcategory}</p> */}
            <p className="product__price">{price} PLN</p>
        </div>
    );
};

export default Product;
