import React from 'react';
import './brands.css';

import Lazer from '../../assets/brands/lazer.png';
import Shimano from '../../assets/brands/shimano.png';
import Romet from '../../assets/brands/romet.png';
import Kls from '../../assets/brands/kls.png';
import Prox from '../../assets/brands/prox.png';
import Kenda from '../../assets/brands/kenda.png';
import Kellys from '../../assets/brands/kellys.png';

const Brands = () => {
    return (
        <section className="section brands" id="brands">
            <h1 className="section__title">Marki</h1>
            <div className="section__container brands__container">
                <div className="brands__image">
                    <img src={Lazer} alt="" />
                </div>
                <div className="brands__image">
                    <img src={Shimano} alt="" />
                </div>
                <div className="brands__image">
                    <img src={Romet} alt="" />
                </div>
                <div className="brands__image">
                    <img src={Kls} alt="" />
                </div>
                <div className="brands__image">
                    <img src={Prox} alt="" />
                </div>
                <div className="brands__image">
                    <img src={Kenda} alt="" />
                </div>
                <div className="brands__image">
                    <img src={Kellys} alt="" />
                </div>
            </div>
        </section>
    );
};

export default Brands;
