import React from 'react';
import { HashLink } from 'react-router-hash-link';
import { useLocation, Link } from 'react-router-dom';
import './header-menu.css';

const HeaderMenu = () => {
    const path = useLocation().pathname;
    const normal = 'header__link';
    const active = 'header__link header__link--active';

    return (
        <ul className="header__menu">
            <li>
                <Link to="/" className={path === '/' ? active : normal}>
                    GWNA
                </Link>
            </li>
            <li>
                <Link
                    to="/oferta"
                    className={path === '/oferta' ? active : normal}>
                    OFERTA
                </Link>
            </li>
            <li>
                <HashLink to="/#brands" className="header__link">
                    MARKI
                </HashLink>
            </li>
            <li>
                <HashLink to="/#contact" className="header__link">
                    KONTAKT
                </HashLink>
            </li>
            <li>
                <Link
                    to="/uslugi"
                    className={path === '/uslugi' ? active : normal}>
                    USUGI
                </Link>
            </li>
        </ul>
    );
};

export default HeaderMenu;
