import React, { useEffect, useState, useRef } from 'react';

import Filter from '../components/filter/Filter';
import Product from '../components/product/Product';

import products from '../data/products';

const Offert = () => {
    const [data, setData] = useState([]);
    const [filters, setFilters] = useState({
        subcategory: 'mountain',
        brands: [],
    });

    let filtered = useRef([]);
    let currentBrands = [];

    console.log('render');

    function changeSubcategory(subcategory) {
        setFilters((prev) => {
            return {
                subcategory,
                brands: [],
            };
        });
    }

    function reset() {
        setFilters((prev) => {
            return {
                ...prev,
                brands: [],
            };
        });
    }

    function addFilter(filter) {
        console.log('add ' + filter);

        setFilters((prev) => {
            return {
                ...prev,
                brands: [...prev.brands, filter],
            };
        });
    }

    function removeFilter(filter) {
        //const i = filters.brands.indexOf(filter);

        console.log('remove ' + filter);

        setFilters((prev) => {
            // if (!prev.brands.includes(filter)) {
            //     console.log('not removed');
            //     return prev;
            // }
            const i = prev.brands.indexOf(filter);
            console.log(i);
            return {
                ...prev,
                brands: prev.brands.splice(i, 1),
            };
        });
    }

    // fetch data
    useEffect(() => {
        setData(products);
    }, []);

    //filter data by category
    filtered = data.filter((el) => {
        if (el.subcategory === filters.subcategory) {
            if (!currentBrands.includes(el.brand)) {
                currentBrands.push(el.brand);
            }
            return el;
        }
    });

    // filter data by brands
    if (filters.brands.length !== 0) {
        filtered = filtered.filter((el) => {
            if (filters.brands.includes(el.brand)) {
                return el;
            }
        });
    }

    // render products
    const productElemenst = filtered.map((product) => {
        return <Product card={product} key={product.id} />;
    });

    // render brand buttons
    const brandsButtons = currentBrands.map((b, i) => {
        const selected = filters.brands.includes(b);
        return (
            <BrandBtn
                selected={selected}
                brand={b}
                add={addFilter}
                remove={removeFilter}
                key={i}
            />
        );
    });

    // all brands button class
    const allBtnClass =
        filters.brands.length > 0
            ? 'category-radio'
            : 'category-radio category-radio--selected';

    return (
        <div className="page-wrapper offert">
            <Filter
                change={changeSubcategory}
                currentCat={filters.subcategory}
            />
            {filtered.length > 0 ? (
                <div>
                    <div className="brands-filter">
                        <button className={allBtnClass} onClick={reset}>
                            Wszystkie
                        </button>
                        {brandsButtons}
                    </div>
                    <div className="products">{productElemenst}</div>
                </div>
            ) : (
                <div className="no-results">
                    <p>Brak wynikw...</p>
                </div>
            )}
        </div>
    );
};

export default Offert;

function BrandBtn(props) {
    const { selected, brand, add, remove } = props;

    const [active, setActive] = useState(false);

    function handleClick() {
        if (active) {
            setActive(false);
            remove(brand);
        } else {
            setActive(true);
            add(brand);
        }
    }

    const btnClass = active
        ? 'category-radio category-radio--selected'
        : 'category-radio';

    return (
        <button className={btnClass} onClick={handleClick}>
            {brand}
        </button>
    );
}
