import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';

import './header.css';

import HeaderMenu from './menu/HeaderMenu';
import MobileMenu from './mobile-menu/MobileMenu';

import { BiMenu } from 'react-icons/bi';

const Header = () => {
    const [headerClass, setHeaderClass] = useState('');
    const [menuOpen, setMenuOpen] = useState(false);

    const handleScroll = () => {
        const position = window.pageYOffset;

        if (position > 100 && position < 600) {
            setHeaderClass('header--hide');
        } else if (position >= 600) {
            setHeaderClass('header--white');
        } else {
            setHeaderClass('');
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll, {
            passive: true,
        });

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const toggleMenu = () => {
        setMenuOpen((prev) => !prev);
    };

    const alwaysWhite =
        useLocation().pathname != '/' ? ' header--always-white' : '';

    return (
        <>
            <MobileMenu open={menuOpen} toggle={toggleMenu} />
            <header className={'header ' + headerClass + alwaysWhite}>
                <nav className="header__nav">
                    <h1 className="header__title">ABC Rowery</h1>
                    <BiMenu
                        className="header__hamburger"
                        onClick={toggleMenu}
                    />
                    <HeaderMenu />
                </nav>
            </header>
        </>
    );
};

export default Header;
