import React from 'react';
import Slider from 'react-slick';
import { ArrowLeft, ArrowRight } from './Arrows';
import './slick.css';
import './sale.css';

import KrossImg from '../../assets/kross/hexagon-7-0_2021.jpg';

const Sale = () => {
    const settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 5,
        slidesToScroll: 5,
        draggable: true,
        touchMove: true,
        swipe: true,
        autoplaySpeed: 1000,
        responsive: [
            {
                breakpoint: 1500,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 4,
                },
            },
            {
                breakpoint: 1000,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                },
            },
            {
                breakpoint: 800,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                },
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                },
            },
        ],
        nextArrow: <ArrowRight />,
        prevArrow: <ArrowLeft />,
    };

    return (
        <div className="sale">
            <h1 className="sale__title">Polecane modele</h1>
            <Slider {...settings}>
                <div className="sale__card">
                    <div className="sale__image-container">
                        <img src={KrossImg} className="sale__image" />
                    </div>
                    <h3 className="sale__name">Kross Hexagon 7.0 2021</h3>
                    <p className="sale__price">3099 PLN</p>
                </div>
                <div className="sale__card">
                    <div className="sale__image-container">
                        <img src={KrossImg} className="sale__image" />
                    </div>
                    <h3 className="sale__name">Kross Hexagon 7.0 2021</h3>
                    <p className="sale__price">3099 PLN</p>
                </div>
                <div className="sale__card">
                    <div className="sale__image-container">
                        <img src={KrossImg} className="sale__image" />
                    </div>
                    <h3 className="sale__name">Kross Hexagon 7.0 2021</h3>
                    <p className="sale__price">3099 PLN</p>
                </div>
                <div className="sale__card">
                    <div className="sale__image-container">
                        <img src={KrossImg} className="sale__image" />
                    </div>
                    <h3 className="sale__name">Kross Hexagon 7.0 2021</h3>
                    <p className="sale__price">3099 PLN</p>
                </div>
                <div className="sale__card">
                    <div className="sale__image-container">
                        <img src={KrossImg} className="sale__image" />
                    </div>
                    <h3 className="sale__name">Kross Hexagon 7.0 2021</h3>
                    <p className="sale__price">3099 PLN</p>
                </div>
                <div className="sale__card">
                    <div className="sale__image-container">
                        <img src={KrossImg} className="sale__image" />
                    </div>
                    <h3 className="sale__name">Kross Hexagon 7.0 2021</h3>
                    <p className="sale__price">3099 PLN</p>
                </div>
            </Slider>
        </div>
    );
};

export default Sale;
