import React from 'react';
import './mobile-menu.css';

import { BiChevronUpCircle } from 'react-icons/bi';

const MobileMenu = (props) => {
    const showClass = props.open ? ' mobile--show' : '';

    return (
        <div className={'mobile' + showClass}>
            <ul className="mobile__menu">
                <li>
                    <a href="/" className="mobile__link mobile__link--active">
                        GWNA
                    </a>
                </li>
                <li>
                    <a href="/oferta" className="mobile__link">
                        OFERTA
                    </a>
                </li>
                <li>
                    <a href="/#brands" className="mobile__link">
                        MARKI
                    </a>
                </li>
                <li>
                    <a href="/#contact" className="mobile__link">
                        KONTAKT
                    </a>
                </li>
                <li>
                    <a href="/uslugi" className="mobile__link">
                        USUGI
                    </a>
                </li>
            </ul>
            <BiChevronUpCircle
                className="mobile__close"
                onClick={() => props.toggle()}
            />
        </div>
    );
};

export default MobileMenu;
